/* src/components/StudentList.css */
.student-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .student-table th,
  .student-table td {
    padding: 12px;
    border: 1px solid #ddd;
    text-align: left;
  }
  
  .student-table th {
    background-color: #007bff;
    color: white;
  }
  
  /* Responsive Table */
  @media (max-width: 768px) {
    .student-table,
    .student-table thead,
    .student-table tbody,
    .student-table th,
    .student-table td,
    .student-table tr {
      display: block;
    }
  
    .student-table tr {
      margin-bottom: 15px;
    }
  
    .student-table th {
      background-color: transparent;
      color: #007bff;
      padding: 8px;
    }
  
    .student-table td {
      position: relative;
      padding: 8px;
      display: flex;
      justify-content: space-between;
    }
  }
  